<template>
  <div>
    <div v-if="Object.keys(pinlun).length !== 0" class="comment-info">
      <div class="info-header">
        <div class="header-title">用户评价</div>
        <div class="header-more">
          更多
          <!-- <i class="arrow-right"></i> -->
        </div>
      </div>
      <div class="info-user">
        <img :src="pinlun.user.avatar" alt />
        <span>{{pinlun.user.uname}}</span>
      </div>
      <div class="info-detail">
        <p>{{pinlun.content}}</p>
        <div class="info-other">
          <span class="date">{{pinlun.created | showDate}}</span>
          <span>{{pinlun.style}}</span>
        </div>
        <div class="info-imgs">
          <img class="auto-img" :src="item" v-for="(item, index) in pinlun.images" :key="index" />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { formatDate } from "../../common/utils";
export default {
  props: {
    pinlun: {
      type: Object,
      default: {}
    }
  },
  filters: {
    showDate(value) {
      //1.将时间戳转成Date对象
      let date = new Date(value * 1000);
      //2.将date进行格式化
      return formatDate(date, "yyyy-MM-dd hh:mm:ss");
    }
  }
};
</script>
<style lang="less" scoped>
.comment-info {
  padding: 5px 12px;
  color: #333;
  background-color: #ffffff;
  border-bottom: 5px solid #f2f5f8;
  .info-header {
    height: 50px;
    line-height: 50px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    .header-title {
      float: left;
      font-size: 15px;
    }

    .header-more {
      float: right;
      margin-right: 10px;
      font-size: 13px;
    }
  }
  .info-user {
    padding: 10px 0 5px;
    img {
      width: 42px;
      height: 42px;
      border-radius: 50%;
    }
    span {
      position: relative;
      font-size: 15px;
      top: -15px;
      margin-left: 10px;
    }
  }
  .info-detail {
    padding: 0 5px 15px;
    p {
      font-size: 14px;
      color: #777;
      line-height: 1.5;
    }
    .info-other {
      font-size: 12px;
      color: #999;
      margin-top: 10px;
      .date {
        margin-right: 8px;
      }
    }
    .info-imgs {
      margin-top: 10px;
      margin-right: 5px;
      img {
        display: inline-block;
        width: 70px;
        height: 70px;
      }
    }
  }
}
</style>