<template>
  <div class="detail">
    <!-- 导航栏 -->
    <div class="detail-nav">
      <div class="detail-img" @click="backclick">
        <img class="auto-img" src="../../assets/img/common/back.svg" alt />
      </div>
      <div class="detail-navitem">
        <div
          class="detail-active"
          :class="{active:item.isActive}"
          v-for="(item,index) in title"
          :key="index"
          @click="titleclick(item,index)"
        >{{item.name}}</div>
      </div>
    </div>
    <scroll class="content" ref="scroll" :probetype="3" @slide="slide">
      <!-- 轮播图组件 -->
      <detail-swiper :swiperimg="swiperimg"></detail-swiper>
      <!-- 商品信息 -->
      <detailitem :goods="gooditem"></detailitem>
      <!-- 店家信息 -->
      <shop-info :shopinfo="shopinfo"></shop-info>
      <!-- 商品图片展示 -->
      <detail-image :detailInfo="detailInfo"></detail-image>
      <!-- 商品参数 -->
      <detail-params ref="params" :itemParams="itemParams"></detail-params>
      <!-- 客户评论 -->
      <detail-pinlun ref="pinluns" :pinlun="pinlun"></detail-pinlun>
      <!-- 推荐列表 -->
      <good-list ref="recommend" :goodlist="goods"></good-list>
    </scroll>
    <back-top @click.native="backtop" v-show="ishas"></back-top>
    <detail-gwc :prop="prop" :skugg="skugg" :priceRange="priceRange" :iid="iid" :titles="titles"></detail-gwc>
  </div>
</template>
<script>
import Scroll from "../../components/common/Scroll";
import DetailSwiper from "./DetailSwipe";
import Detailitem from "./Detailitem";
import ShopInfo from "./ShopInfo";
import DetailImage from "./DetailImage";
import DetailParams from "./DetailParams";
import DetailPinlun from "./DetailPinlun";
import GoodList from "../../components/content/goods/GoodList";
import BackTop from "../../components/content/backtop/backTop";
import DetailGwc from "./DetailGwc";

import { debounce } from "../../common/utils";
import { getdetailitem, Goods, getrecommed } from "../../network/category";

export default {
  name: "Detail",
  components: {
    DetailSwiper,
    Detailitem,
    ShopInfo,
    Scroll,
    DetailImage,
    DetailParams,
    DetailPinlun,
    GoodList,
    BackTop,
    DetailGwc
  },
  data() {
    return {
      title: [
        { name: "商品", isActive: true },
        { name: "参数", isActive: false },
        { name: "评论", isActive: false },
        { name: "推荐", isActive: false }
      ],
      swiperimg: [],
      gooditem: {},
      shopinfo: {},
      detailInfo: {},
      itemParams: {},
      pinlun: {},
      goods: [],

      //按钮跳转值
      themsaveY: [],

      getthemY: null,

      currentindex: 0,

      ishas: false,

      //购物
      prop: [],

      skugg: [],

      priceRange: "",

      iid: "",

      titles: ""
    };
  },
  created() {
    // 
    let iid = this.$route.query.id;
    this.iid = iid;
    //请求详情的数据
    getdetailitem(iid)
      .then(result => {
        // 
        const data = result.data.result;
        //获取轮播图数据
        this.swiperimg = data.itemInfo.topImages;

        //创建 new 一个 对象
        this.gooditem = new Goods(
          data.itemInfo,
          data.columns,
          data.shopInfo.services
        );
        // 

        //获取店铺信息
        this.shopinfo = data.shopInfo;

        //获取详情图片
        this.detailInfo = data.detailInfo;

        //获取参数信息
        this.itemParams = data.itemParams;
        

        //获取评论信息
        // 
        if (data.rate.cRate !== 0) {
          this.pinlun = data.rate.list[0];
        }

        //购物选择
        this.prop = data.skuInfo.props;
        this.skugg = data.skuInfo.skus;
        this.priceRange = data.skuInfo.priceRange;
        this.titles = data.skuInfo.title;
        // 
        // 
      })
      .catch(err => {
        
      }),
      //请求推荐数据
      getrecommed()
        .then(result => {
          this.goods = result.data.data.list;
          // 
        })
        .catch(err => {
          
        });

    this.getthemY = debounce(() => {
      try {
        this.themsaveY = [];
        this.themsaveY.push(0);
        this.themsaveY.push(this.$refs.params.$el.offsetTop);
        this.themsaveY.push(this.$refs.pinluns.$el.offsetTop);
        this.themsaveY.push(this.$refs.recommend.$el.offsetTop);

        // 
      } catch (err) {
        // 
      }
    }, 200);
  },
  mounted() {
    //接收，然后刷新图片   函数防抖
    const refresh = debounce(this.$refs.scroll.refresh, 100);
    this.$bus.$on("LoadImage", () => {
      refresh();
      this.getthemY();
    });

    //赋值 获取offerstop
    // 
  },
  methods: {
    // 点击按钮回到顶部
    backtop() {
      this.$refs.scroll.scrolltop(0, 0);
      // 
    },
    //监听滚动
    slide(position) {
      let positions = -position.y;
      // 
      this.ishas = -position.y > 1000;

      let length = this.themsaveY.length;
      for (let i = 0; i < length; i++) {
        if (
          this.currentindex !== i &&
          ((i < length - 1 &&
            positions >= this.themsaveY[i] &&
            positions < this.themsaveY[i + 1]) ||
            (i === length - 1 && positions > this.themsaveY[i]))
        ) {
          this.currentindex = i;

          for (let j = 0; j < this.title.length; j++) {
            this.title[j].isActive = false;
            if (this.currentindex == j) {
              this.title[j].isActive = true;
            }
          }
          // 
        }
      }
    },
    //监听导航栏
    titleclick(item, index) {
      if (item.isActive) {
        return;
      }
      for (let i = 0; i < this.title.length; i++) {
        if (this.title[i].isActive) {
          this.title[i].isActive = false;
          break;
        }
      }
      item.isActive = true;

      //
      this.$refs.scroll.scrolltop(0, -this.themsaveY[index], 1000);
    },
    //返回
    backclick() {
      this.$router.go(-1);
    }
  }
};
</script>
<style lang="less" scoped>
.detail {
  height: 100vh;
  width: 100vw;
  position: relative;
  background-color: #f2f5f8;
  z-index: 10;
}
.content {
  height: calc(~"100% - 94px");
  background-color: #f2f5f8;
  margin-top: 44px;
}
.detail-nav {
  width: 100vw;
  z-index: 11;
  top: 0;
  left: 0;
  line-height: 44px;
  height: 44px;
  position: fixed;
  background-color: #ffffff;
  border-bottom: 1px solid #e8e8e8;
  font-size: 14px;
  display: flex;
  .detail-img {
    margin: auto 0;
    width: 22px;
    height: 22px;
  }
  .detail-navitem {
    width: calc(~"100% - 62px");
    padding: 0 20px;
    display: flex;
    justify-content: space-around;
    text-align: center;
    .detail-active {
      width: 25%;
      &.active {
        color: salmon;
      }
    }
  }
}
</style>