<template>
  <div v-if="Object.keys(shopinfo).length !== 0" class="shopinfo">
    <div class="imgs">
      <div class="img">
        <img class="auto-img" :src="shopinfo.shopLogo" alt />
      </div>
      <div class="img-name">{{shopinfo.name}}</div>
    </div>
    <div class="baby">
      <div class="baby-left">
        <div>
          <p>{{shopinfo.cSells | mollion}}</p>
          <p>总销量</p>
        </div>
        <div>
          <p>{{shopinfo.cGoods}}</p>
          <p>全部宝贝</p>
        </div>
      </div>
      <div class="shop-middle-item shop-middle-right">
        <table>
          <tr v-for="(item,index) in shopinfo.score" :key="index">
            <td>{{item.name}}</td>
            <td class="score" :class="{'score-better':item.isBetter}">{{item.score}}</td>
            <td class="better" :class="{'better-more':item.isBetter}">
              <span>{{item.isBetter ? '高':'低'}}</span>
            </td>
          </tr>
        </table>
      </div>
    </div>
    <div class="shop-bottom">进店逛逛</div>
  </div>
</template>
<script>
export default {
  props: {
    shopinfo: {
      type: Object,
      default: {}
    }
  },
  filters: {
    mollion(value) {
      if (value < 10000) {
        return value;
      } else {
        return (value / 10000).toFixed(1) + "万";
      }
    }
  }
};
</script>
<style lang="less" scoped>
.shopinfo {
  margin-top: 6px;
  background: #ffffff;
  padding: 0 10px;
  display: flex;
  flex-wrap: wrap;
  .imgs {
    width: 100%;
    display: flex;
    line-height: 46px;
    margin-bottom: 10px;
    .img {
      width: 46px;
      height: 46px;
      margin-right: 6px;
    }
    .img-name {
      font-size: 20px;
    }
  }
  .baby {
    width: 100%;
    height: 80px;
    display: flex;
    margin-bottom: 26px;
    .baby-left {
      width: 50%;
      display: flex;
      > div {
        width: 50%;
        text-align: center;
        font-size: 14px;
        > p {
          margin-top: 10px;
        }
      }
    }
  }
  .shop-middle-right {
    font-size: 13px;
    color: #333;
    table {
      width: 120px;
      margin-left: 30px;
    }
    table td {
      padding: 5px 0;
    }
    .score {
      color: #5ea732;
    }
    .score-better {
      color: #f13e3a;
    }
    .better span {
      background-color: #5ea732;
      color: #fff;
      text-align: center;
    }
    .better-more span {
      background-color: #f13e3a;
    }
  }
  .shop-bottom {
    text-align: center;
    line-height: 30px;
    width: 150px;
    height: 30px;
    margin-bottom: 20px;
    border-radius: 20px;
    background-color: salmon;
    margin-left: calc((100% - 150px) / 2);
  }
}
</style>