<template>
  <div v-if="Object.keys(swiperimg).length !== 0" class="detailswiper">
    <!-- Swiper轮播图 -->
    <van-swipe :autoplay="3000" class="swipe">
      <van-swipe-item v-for="(vaule, index) in swiperimg" :key="index" @load="LoadImage">
        <img class="auto-img" :src="vaule" />
      </van-swipe-item>
    </van-swipe>
  </div>
</template>
<script>
export default {
  props: {
    swiperimg: {
      type: Array,
      default: []
    }
  },
  methods: {
    LoadImage() {
      this.$bus.$emit("LoadImage");
    }
  }
};
</script>
<style lang="less" scoped>
.swipe {
  height: 300px;
}
</style>