<template>
  <div v-if="Object.keys(detailInfo).length !== 0" class="detailimage">
    <div class="info-desc">
      <div class="start fl"></div>
      <div class="desc">{{detailInfo.desc}}</div>
      <div class="end fr"></div>
    </div>
    <div class="info-key">{{detailInfo.detailImage[0].key}}</div>
    <div class="info-list">
      <img
        class="auto-img"
        v-for="(item,index) in detailInfo.detailImage[0].list"
        :key="index"
        v-lazy="item"
        @load="Detailimage"
        alt
      />
    </div>
  </div>
</template>
<script>
export default {
  props: {
    detailInfo: {
      type: Object,
      default: {}
    }
  },
  methods: {
    //监听图片是否加载完成
    Detailimage() {
      // 
      this.$bus.$emit("LoadImage");
    }
  }
};
</script>
<style lang="less" scoped>
.detailimage {
  background-color: #ffffff;
  margin-top: 5px;
  border-bottom: 5px solid #f2f5f8;
  padding: 6px 6px 0 6px;
  .info-desc {
    .desc {
      padding: 15px 0;
      font-size: 14px;
    }
    .start,
    .end {
      width: 90px;
      height: 1px;
      background-color: #a3a3a5;
      position: relative;
    }
    .start::before,
    .end::after {
      content: "";
      position: absolute;
      width: 5px;
      height: 5px;
      background-color: #333;
      bottom: 0;
    }
    .end::after {
      right: 0;
    }
  }
  .info-key {
    margin: 10px 0 10px 15px;
    color: #333;
    font-size: 15px;
  }
}
</style>