<template>
  <div class="detailgwc">
    <van-goods-action>
      <van-goods-action-icon @click="gocar" icon="cart-o" text="购物车" :badge="shopnum.length" />
      <van-goods-action-icon icon="shop-o" text="店铺" />
      <van-goods-action-button @click="gwc" type="warning" text="加入购物车"></van-goods-action-button>
      <van-goods-action-button @click="gm" type="danger" text="立即购买" />
    </van-goods-action>
    <van-sku
      v-model="show"
      :sku="sku"
      :goods="goods"
      @buy-clicked="onBuyClicked"
      @add-cart="onAddCartClicked"
    />
  </div>
</template>

<script>
import { Toast } from "vant";

//命名空间用法
import { createNamespacedHelpers } from "vuex";
const { mapState, mapMutations } = createNamespacedHelpers("shopcart");

let sku = {
  // 所有sku规格类目与其值的从属关系，比如商品有颜色和尺码两大类规格，颜色下面又有红色和蓝色两个规格值。
  // 可以理解为一个商品可以有多个规格类目，一个规格类目下可以有多个规格值。
  tree: [],
  // 所有 sku 的组合列表，比如红色、M 码为一个 sku 组合，红色、S 码为另一个组合
  list: [],
  price: "1.00", // 默认价格（单位元）
  stock_num: 227, // 商品总库存
  collection_id: 2261, // 无规格商品 skuId 取 collection_id，否则取所选 sku 组合对应的 id
  none_sku: false, // 是否无规格商品
  hide_stock: false // 是否隐藏剩余库存
};
export default {
  props: {
    prop: {
      type: Array,
      default: []
    },
    skugg: {
      type: Array,
      default: []
    },
    priceRange: {
      type: String,
      default: ""
    },
    iid: {
      type: String,
      default: ""
    },
    titles: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      show: false,
      sku: sku,
      goods: {
        // 默认商品 sku 缩略图
        picture: ""
      },
      messageConfig: {
        // 数据结构见下方文档
      },
      tree: "",
      img: ""
    };
  },
  computed: {
    ...mapState(["shopnum"])
  },
  methods: {
    //vuex
    ...mapMutations(["getshopdata"]),

    //购物车选择参数
    gwc() {
      let token = localStorage.getItem("_TNK");
      if (!token) {
        Toast("请注册登录");
        setTimeout(() => {
          this.$router.push({ name: "Register" });
        }, 1000);
        return;
      }
      this.choosesku();
    },

    //购买选择参数
    gm() {
      let token = localStorage.getItem("_TNK");
      if (!token) {
        Toast("请注册登录");
        setTimeout(() => {
          this.$router.push({ name: "Register" });
        }, 1000);
        return;
      }
      this.choosesku();
    },

    //跳转到购物车
    gocar() {
      this.$router.push({ name: "Car" });
    },
    //选择参数
    choosesku() {
      this.show = true;
      // 
      // 
      // 

      this.sku.price = this.priceRange.slice(1);
      // 
      var tree = [];
      this.prop.forEach((element, index) => {
        var arr = [];
        element.list.forEach((v, i) => {
          if (v.styleId) {
            arr.push({
              id: v.name,
              name: v.name
            });
          } else {
            arr.push({
              id: v.name,
              name: v.name
            });
          }
        });
        tree.push({
          k: element.label,
          k_s: "s" + index,
          v: arr,
          largeImageMode: true
        });
      });

      for (let i = 0; i < this.skugg.length; i++) {
        tree.forEach((h, j) => {
          h.v.forEach((v, j) => {
            if (v.id == this.skugg[i].style) {
              v.imgUrl = this.skugg[i].img;
              v.previewImgUrl = this.skugg[i].img;
            }
          });
        });
      }
      this.sku.tree = tree;
      this.goods.picture = tree[0].v[0].imgUrl;
      var list = [];
      this.skugg.forEach(ele => {
        list.push({
          id: ele.stockId,
          s0: ele.style,
          s1: ele.size,
          price: ele.nowprice,
          stock_num: ele.stock
        });
      });
      this.sku.list = list;
      this.tree = tree;
    },
    //加入购物车
    onAddCartClicked(skuData) {
      
      for (let i = 0; i < this.tree[0].v.length; i++) {
        if (this.tree[0].v[i].name == skuData.selectedSkuComb.s0) {
          this.img = this.tree[0].v[i].imgUrl;
          break;
        }
      }

      const product = {};
      product.iid = this.iid;
      product.stockId = skuData.selectedSkuComb.id;
      product.price = skuData.selectedSkuComb.price / 100;
      product.count = skuData.selectedNum;
      product.style = skuData.selectedSkuComb.s0;
      product.size = skuData.selectedSkuComb.s1;
      product.title = this.titles;
      product.img = this.img;

      this.getshopdata(product);
    },
    // 立即购买   跳到结算页面结算页面
    onBuyClicked(skuData) {
      for (let i = 0; i < this.tree[0].v.length; i++) {
        if (this.tree[0].v[i].name == skuData.selectedSkuComb.s0) {
          this.img = this.tree[0].v[i].imgUrl;
          break;
        }
      }

      const product = {};
      product.iid = this.iid;
      product.stockId = skuData.selectedSkuComb.id;
      product.price = skuData.selectedSkuComb.price / 100;
      product.count = skuData.selectedNum;
      product.style = skuData.selectedSkuComb.s0;
      product.size = skuData.selectedSkuComb.s1;
      product.title = this.titles;
      product.img = this.img;
      product.isActive = true

      var shopping = [];
      shopping.push(product);

      this.$router.push({ name: "Gocar", query: { shopping: shopping } });


    }
  }
};
</script>
<style lang="less" scoped>
.detailgwc {
  /deep/ .van-sku__price-symbol {
    font-size: 22px;
  }
}
</style>